@media only screen and (min-width: 768px) {
    /* For desktop: */
    .contact-section {
        padding: 0 15%;
        padding-top: 2rem;
        padding-bottom: 4rem;
        text-align: center;
    }
    
    .contact-section a {
        color: #666;
    }
    
    .contact-blocks {
        display: flex;
    }
    
    .contact-block {
        display: grid;
        grid-template-columns: 2.2rem auto;
        grid-row-gap: 1rem;
        grid-column-gap: 1rem;
        text-align: left;
    }
    
    .contact-grid-item {
        display: flex;
        align-items: center;
        font-size: 1.5rem;
    }
}

/* Mobile */
.mobile-contact-section {
    padding: 0 10px;
    padding-top: 1rem;
    padding-bottom: 2rem;
    text-align: center;
}

.mobile-contact-section a {
    color: #666;
}

.mobile-contact-blocks {
    display: flex;
}

.mobile-contact-block {
    display: grid;
    grid-template-columns: 2.2rem auto;
    grid-row-gap: 1rem;
    grid-column-gap: 1rem;
    text-align: left;
}

.mobile-contact-grid-item {
    display: flex;
    align-items: center;
    font-size: 1.3rem;
}