@media only screen and (min-width: 768px) {
    /* For desktop: */
    li {
        display: inline;
        padding: 2px 20px;
        font-size: 1.2rem;
    }
    
    a {
        text-decoration: none;
        color: #ddd;
    }
    
    a:hover {
        color: white;
    }
    
    .header-box {
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        display: flex;
        align-items: center;
        justify-content: space-between;
        background-color: #000;
        color: #fff;
        padding: 10px 4rem;
        z-index: 1;
    }
    
    .logo {
        display: flex;
        align-items: center;
        font-size: 1.3rem;
        font-weight: bold;
    }
    
    .navigation-menu {
        
    }
}


/* Mobile */
a {
    text-decoration: none;
    color: #ddd;
}

a:hover {
    color: white;
}

.header-box {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: #000;
    color: #fff;
    padding: 5px 5px;
    z-index: 1;
    box-sizing: border-box;
}

.mobile-header-logo {
    width: 30px;
}

.mobile-menu-btn {
    color: white;
    background: none;
    border: none;
    margin-right: 10px;
    padding: 5px;
}

.mobile-header-icon {
    font-size: 20px;
}

.mobile-dropdown-menu {
    position: absolute;
    top: 44px;
    right: 0;
    width: 50%;
    background: black;
    border: 1px solid white;
}

.mobile-dropdown-menu ul {
    list-style-type: none;
}

.mobile-dropdown-menu a {
    display: block;
    padding: 5px;
}

.mobile-dropdown-menu a:hover {
    background: #222;
    color: white;
}