@media only screen and (min-width: 768px) {
    /* For desktop: */
    .skills-section {
        padding-top: 2rem;
        padding-bottom: 4rem;
        text-align: center;
    }
    
    .skill-blocks {
        display: flex;
    }
    
    .skills-section li {
        display: block;
        font-size: 1.5rem;
        margin: 20px 0;
    }
    
    .blocks {
        flex: 1;
    }
}

/* Moblie */
.mobile-skills-section {
    padding-top: 2rem;
    padding-bottom: 1rem;
    text-align: center;
}

.mobile-skill-blocks {
    display: flex;
}

.mobile-skills-section li {
    display: block;
    font-size: 1.2rem;
    margin: 20px 0;
}

.mobile-blocks {
    flex: 1;
}